import React, { useState } from 'react'

function AddToDo({addTodo}) {
    let submit = (e) => {
        e.preventDefault();
        if(name && desc) {
            addTodo(name, desc);
            setName("");
            setDesc("");
        } else {
            alert("Name and Description can't be empty!")
        }
    };
    const [name, setName] = useState("")
    const [desc, setDesc] = useState("")
    return (
        <form className="container mb-4" onSubmit={submit}>
            <div class="form-group">
                <label htmlFor="title">Name:</label>
                <input type="text" value={name} onChange={(e) => {setName(e.target.value)}} class="form-control mb-3" id="title" aria-describedby="title" placeholder="Person Name"/>
            </div>
            <div class="form-group">
                <label htmlFor="description">What Happened:</label>
                <input type="text" value={desc} onChange={(e) => {setDesc(e.target.value)}} class="form-control" id="description" placeholder="What that person did to you?"/>
            </div>
            
            <button type="submit" class="btn btn-success btn-sm mt-3">Add Hishab</button>
        </form>
    )
}

export default AddToDo
