import React from 'react'

function ToDoItem({sn, name, desc, remove}) {
    return (
        <label className="list-group-item">
            <div className="d-flex gap-3">
                <span className="flex-shrink-0"  style={{fontSize: 1.375 + 'em'}} >{sn}</span>
                <span className="pt-1 form-checked-content">
                    <strong>{name}</strong>
                    <small className="d-block text-muted">
                        {desc}
                    </small>
                </span>
            </div>
            
            <div className="d-flex gap-3 flex-row-reverse">
                <button type="submit" className="btn btn-danger btn-sm mt-3 " onClick={() => remove(sn)}>Delete</button>
            </div>
        </label>
    )
}

export default ToDoItem
