import React from 'react'
import ToDoItem from './ToDoItem'



function ToDos({hishab, remove}) {
    return (
        <div className="list-group container">
            {hishab.length===0? <h4 className="container mb-3">No Poi Poi Hishab has been added yet! Add a Hishab to create the Poi Poi Hishab List</h4> : hishab.map((todo) => <ToDoItem key={todo.sn} name={todo.name} desc={todo.desc} sn={todo.sn} remove={remove}/>)}
        </div>
    )
}

export default ToDos
