import logo from './logo.svg';
import './App.css';
import Header from './Base_Components/Header';
import Footer from './Base_Components/Footer';
import AddToDo from './Base_Components/AddToDo';
import ToDos from './Base_Components/ToDos';
import About from './Base_Components/About';
import { useState, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

function App() {
  let initPoiPoi;
  if(localStorage.getItem('poipoi')){
      initPoiPoi = JSON.parse(localStorage.getItem('poipoi'))
      
  } else {
      initPoiPoi = []
  }
  let remove = (sn) => {
    setHishab(hishab.filter((e) => {
      return e.sn != sn;
    }))
  };

  let addTodo = (name, desc) => {
    let sn;
    if (hishab.length===0){
      sn = 1;
    } else {
      sn = hishab[hishab.length-1].sn + 1;
    }
    const todo = {
      sn: sn,
      name: name,
      desc: desc,
    }

    setHishab([...hishab, todo])
  };
  const [hishab, setHishab] = useState(initPoiPoi)

  useEffect(() => {
    localStorage.setItem('poipoi', JSON.stringify(hishab))
  }, [hishab])
  return (
    <Router>
      <Header/>
      <Switch>
        <Route exact path="/Poi-Poi-Hishab/">
          <>
          <h2 className="container mb-3 mt-3">Add Poi Poi Hishab:</h2>
          <AddToDo addTodo={addTodo} />
          <hr/>
          <h2 className="container mb-3">Poi Poi Hishab List:</h2>
          <ToDos hishab={hishab} remove={remove}/>
          </>
        </Route>
        <Route exact path="/Poi-Poi-Hishab/about">
          <About/>
        </Route>
      </Switch>
      
      <Footer/>
    </Router>
  );
}

export default App;
